@import url('https://fonts.googleapis.com/css2?family=Chakra+Petch:wght@600&display=swap');

*
{
    margin: 0;
    padding: 0;
}

html,
body {
    overflow: hidden;
}

.webgl {
    position: fixed;
    top: 0;
    left: 0;
    outline: none;    
}

.welcome-container{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
}

.title-container {
    position: absolute;
    width: 50%;
    height: auto;
    top: 50%;
    left: 0;
    transform: translateY(-50%);


}

.title {
    color: whitesmoke;
    padding-left: 4rem;

}

.title h1 {
    font-family: 'Chakra Petch', sans-serif;
    color: whitesmoke;
    font-size: 5rem;
    pointer-events: none;
}

.title p {
    font-family: 'Chakra Petch', sans-serif;
    color: whitesmoke;
    font-size: 2rem;
    pointer-events: none;
    
}

.explore {
    cursor: pointer;
    padding: 0.5rem 1rem;
    margin-top: 2rem;
    border: none;
    border-radius: 3px;
    font-weight: 500;
    background-color: #0072ff;
    /* background-color: #00fff3; */
    /* box-shadow: #00fff3 0px 25px 50px -12px; */
    text-transform: uppercase;
    font-family: 'Chakra Petch', sans-serif;
    color: whitesmoke;
}
 .modal {
    position: fixed;
    height: 100vh;
    width: 40%;
    top: 0;
    right: 0;
    color: whitesmoke;
    background: transparent;
    box-shadow: inset 0 4px 15px 0 #0072ff, inset 0 6px 150px 0 #0072ff, 10px 4px 15px 0 #a9c7eb, 0 6px 150px 0 #0072ff;
 }
